"use strict";

//点击跳转详情页
// 事件委托
var hide_timer = "";
$("#comonjakeby .hot-list-duct ").on("mouseenter", function () {
  clearTimeout(hide_timer);
  hide_timer = setTimeout(function () {
    $(".hot-list-duct").css({
      background: "#f9f9f9",
      cursor: "pointer"
    });
  }, 200);
});
$("#comonjakeby .hot-list-duct").on("mouseleave", function () {
  clearTimeout(hide_timer);
  console.log(1);
  $(".hot-list-duct").css({
    background: "#ffffff"
  });
});
$("#comonjakeby .hot-list-duct ").on("click", ".item-cover", function () {
  // console.log(1)
  location.href = "../html/detail.html";
});